@import "color.sass";
.trademark {
  background: url("../img/Background-1.jpg") no-repeat;
  width: 100%;
  height: 500px;
  background-size: 100%;
  background-position: 0 25%;
  // @media(max-width: 1300px)
  //   background-size: cover
  @media(max-width: 767px) {
    background-position: 60% 0;
    background-size: cover; }
  .container, .row {
    height: 100%; }
  &__content {
    @media(max-width: 767px) {
      padding: 0 30px;
      h1 {
        font-size: 34px; } }
    h1 {
      color: $white;
      padding-top: 120px;
      font-size: 40px;
      font-weight: 500;
      max-width: 515px; }
    p {
      color: $white;
      padding-top: 15px;
      padding-bottom: 50px;
      font-weight: 100;
      font-family: 'Muller';
      font-size: 20px;
      max-width: 515px; }
    a {
      display: inline-block;
      padding: 15px 55px;
      border: 3px solid $white;
      border-radius: 4px;
      font-size: 20px;
      font-weight: 300;
      color: $white;
      margin-bottom: 70px;
      transition: all 0.4s;
      &:hover {
        color: $purple;
        background: $white; }
      @media(max-width: 767px) {
        padding: 13px 50px;
        font-size: 15px; } } } }
